//   Education as EducationView,
//   Settings as SettingsView,
//   SignIn as SignInView,
//   SignUp as SignUpView,
//   MailVerification as MailVerificationView,
//   ResetPassword as ResetPasswordView,
//   ForgotPassword as ForgotPasswordView,
//   NotFound as NotFoundView,
// } from './views';
const DashboardView = React.lazy(() => import("./views/Dashboard"));
const SwapView = React.lazy(() => import("./views/Swap"));
const SendView = React.lazy(() => import("./views/Send"));
const WalletView = React.lazy(() => import("./views/Wallet"));
const AddressView = React.lazy(() => import("./views/Address"));
const NftsView = React.lazy(() => import("./views/Nfts"));
const SingleNFTView = React.lazy(() => import("./views/SingleNFT"));
const EducationView = React.lazy(() => import("./views/Education"));
const SettingsView = React.lazy(() => import("./views/Settings"));
const SignInView = React.lazy(() => import("./views/SignIn"));
const SignUpView = React.lazy(() => import("./views/SignUp"));

const MailVerificationView = React.lazy(() => import("./views/MailVerification"));
const ResetPasswordView = React.lazy(() => import("./views/ResetPassword"));
const ForgotPasswordView = React.lazy(() => import("./views/ForgotPassword"));
const NotFoundView = React.lazy(() => import("./views/NotFound"));

const useStyles = makeStyles((theme) => ({
  loading: {
    display: 'flex', 
    justifyContent: 'center', 
    marginTop: 20,
    width: '100% !important',
    color: theme.palette.blue
  }
}));
const Routes = () => {
  const classes = useStyles();
  return (
    <React.Suspense fallback={
      <CircularProgress 
        className={classes.loading}
      />
    }>
    <Switch>
      {/* <ScrollToTop>  */}
        <Redirect exact from="/" to="/login"/>
        <RouteWithLayout
          component={DashboardView}
          exact
          layout={MainLayout}
          title='Good Afternoon'
          description='Welcome to Clutch Connect, a powerful, secure and simple DeFi platform.'
          path="/dashboard"
        />
        <RouteWithLayout
          component={SwapView}
          exact
          layout={MainLayout}
          title='Swap'
          description='Easily swap one crypto for another'
          path="/swap"
        />
        <RouteWithLayout
          component={SendView}
          exact
          layout={MainLayout}
          title='Send Crypto'
          description="Send crypto to another wallet"
          path="/send"
        />
        <RouteWithLayout
          component={AddressView}
          exact
          layout={MainLayout}
          title='Address Book'
          description="Save the wallets you interact with"
          path="/address"
        />
        <RouteWithLayout
          component={NftsView}
          exact
          layout={MainLayout}
          title="NFTs"
          // description="List of NFT pictures that are in your wallet"
          path="/nfts"
        />
        <RouteWithLayout
          component={SingleNFTView}
          exact
          layout={MainLayout}
          title=" "
          path="/nfts/:asset_contract_address/:token_id"
        />
        <RouteWithLayout
          component={WalletView}
          exact
          layout={MainLayout}
          title="Wallets"
          description="View and manage your connected wallets"
          path="/wallets"
        />
        <RouteWithLayout
          component={EducationView}
          exact
          layout={MainLayout}
          title="Education"
          path="/education"
        />
        <RouteWithLayout
          component={SettingsView}
          exact
          layout={MainLayout}
          title="Settings"
          path="/settings"
        />
        <RouteWithLayout
          component={SignInView}
          exact
          layout={AuthLayout}
          path="/login"
        />
        <RouteWithLayout
          component={SignUpView}
          exact
          layout={AuthLayout}
          path="/register"
        />
        <RouteWithLayout
          component={ForgotPasswordView}
          exact
          layout={AuthLayout}
          path="/forgot-password"
        />
        <RouteWithLayout
          component={ResetPasswordView}
          exact
          layout={AuthLayout}
          path="/reset-password/:token"
        />
        <RouteWithLayout
          component={MailVerificationView}
          exact
          layout={AuthLayout}
          path="/verify/:token" 
        />
        <RouteWithLayout
          component={NotFoundView}
          exact
          layout={NormalLayout}
          path="/not-found"
        />
        <Redirect to="/not-found" />
      {/* </ScrollToTop> */}
    </Switch>
    </React.Suspense>
  );
};

export default Routes;
